import * as ActionTypes from '../const/ActionTypes';

export function add() {
  return {
    type: ActionTypes.ADD
  };
}

export function remove() {
  return {
    type: ActionTypes.REMOVE
  };
}

export function restartGames() {
  return {
    type: ActionTypes.RESTART_GAMES
  };
}

export function moveLeft() { // waction
  return {
    type: ActionTypes.MOVE_LEFT
  };
}

export function moveRight() { // waction
  return {
    type: ActionTypes.MOVE_RIGHT
  };
}

export function moveUp() { // waction
  return {
    type: ActionTypes.MOVE_UP
  };
}

export function moveDown() { // waction
  return {
    type: ActionTypes.MOVE_DOWN
  };
}

export function standStill() { // 
  return {
    type: ActionTypes.STAND_STILL
  };
}



